<template>
  <div id="main" class="wrapper">

    <input type="text" v-model="musicName" class="searchBox" placeholder="歌曲/歌手"/>
    <button v-on:click="getData(musicName)" class="searchBtn">搜索</button>
    <div class="songList">
      <div v-show="!isMusic" class="noMusic">
        <img src="../assets/noMusic.png" alt="*" class="icon">
        暂无音乐，请搜索！
      </div>
      <ul v-show="isMusic">
        <li v-for="(item,index) in songList" v-show="songList" v-on:click="setMusicUrl(index)">
          <div class="musicBar">
            <p>{{item.SongName}}</p>
            <p>{{item.SingerName}}</p>
          </div>
        </li>
      </ul>

    </div>
    <div class="audioBar">
      <audio v-bind:src="musicUrl" controls="controls" loop="loop" autoplay="autoplay" controlsList="nodownload">亲 您的浏览器不支持html5的audio标签</audio>
    </div>

  </div>
</template>
<script>
  import $ from "jquery"
  import resize from "../tools/resize";

  export default ({
    data() {
      return {
        musicName: "",
        songList: [],
        data: "",
        musicId: "",
        musicUrl: "http://fs.w.kugou.com/201710311435/2784647bce28c10808e204f66f88680b/G001/M0B/12/19/QQ0DAFS43jOATIosADaWAFwMkd8070.mp3",
        singer: "",
        width: "",
        height: "",
        isMusic: false
      }

    },
    created: function(){
//      document.body.addEventListener('touchmove', function (event) {
//        event.preventDefault();
//      }, false);
    },
    methods: {
      getData: function (songName) {
        let self = this;
        $.ajax({
//          url: `/api/search/get/?s=${songName}&limit=40&type=1&offset=0`,
          url: `/song_search_v2?keyword=${songName}&page=1&clientver=&platform=WebFilter`,
          type: "GET",
          dataType: "json",
          success: function (res) {
            self.data = res.data;
            self.songList = self.data.lists;
            self.isMusic = true;
          },
          error: function () {
            console.log(arguments);
          }
        })
      },
      setMusicUrl: function (index) {
        let self = this;
        self.musicId = self.songList[index].FileHash;
        $.ajax({
//          url: `/api/search/get/?s=${songName}&limit=40&type=1&offset=0`,
          url: `/yy/index.php?r=play/getdata&hash=${self.musicId}`,
          type: "GET",
          dataType: "json",
          success: function (res) {
            self.musicUrl = res.data.play_url;
            console.log(self.musicUrl)
          },
          error: function () {
            console.log(arguments);
          }
        })
      },

    }
  })

</script>
<style lang="scss" scoped="">
  @import "../scss/_size.scss";

  * {
    margin: 0;
    padding: 0;
  }

  .wrapper {
    width: px(375);
    min-height: 100%;
    position: relative;
    box-sizing: border-box;
    text-align: center;
    background: url("../assets/timg1.jpg") no-repeat;
    background-size: cover;

    .searchBox {
      margin-top: px(30);
      padding-left: px(20);
      width: px(230);
      height: px(50);
      font-size: px(16);
      border-radius: px(10);
      border: 0;
      outline: none;
      color: #fff;
      background: rgba(158, 109, 74, 0.5);
    }
    .searchBtn {
      border-radius: px(10);
      border: 0;
      width: px(100);
      height: px(50);
      background: rgba(158, 109, 74, 0.5);
      font-size: px(16);
      color: #fff;
      outline: none;
    }
    .songList {
      width: px(350);
      margin: px(10) auto;
      padding: px(5);
      height: px(400);
      border: 1px solid #ddd;
      overflow: auto;
      border-radius: px(10);
      .noMusic{
        width: px(200);
        margin: px(40) auto;
        display: flex;
        height: px(20);
        line-height: px(20);
        color: #fff;
        .icon{
          display: block;
          width: px(20);
          height:px(20);
          margin-right: px(5);
        }
      }
      ul {
        padding-top: px(10);
        list-style: none;

        li {
          padding-left: px(10);
          display: flex;
          align-items: center;
          cursor: pointer;
          text-align: left;
          font-size: px(12);
          padding-top: px(15);
          padding-bottom: px(15);
          border-bottom: 1px solid #ddd;

          .musicBar {
            p:nth-child(1) {
              font-size: px(16);
              color: #fff;
              font-weight: 400;
            }
            p:nth-child(2) {
              font-size: px(13);
              color: #fff;
            }

          }
        }
        li:nth-child(1) {
          border-top: 1px solid #ddd;
        }
      }

    }
    .audioBar{
      width:100%;
      position: fixed;
      bottom: 0;
      audio{
        width:100%;
        height: px(100);
      }

    }
  }

</style>



